(function (self, Prism) {
    if (typeof self === 'undefined' || !self.Prism || !self.document) {
        return;
    }

    // The languages map is built automatically with gulp
    var Languages = {'html': 'HTML', 'css': 'CSS', 'clike': 'C-like', 'javascript': 'JavaScript', 'abap': 'ABAP',
'actionscript': 'ActionScript', 'apacheconf': 'Apache Configuration', 'apl': 'APL', 'applescript': 'AppleScript',
'aspnet': 'ASP.NET (C#)', 'autoit': 'AutoIt', 'autohotkey': 'AutoHotkey',
'basic': 'BASIC', 'csharp': 'C#', 'cpp': 'C++', 'coffeescript': 'CoffeeScript',
'css-extras': 'CSS Extras', 'fsharp': 'F#', 'glsl': 'GLSL', 'http': 'HTTP', 'inform7': 'Inform7',
'latex': 'LaTeX', 'lolcode': 'LOLCODE', 'matlab': 'MATLAB', 'mel': 'MEL', 'nasm': 'NASM', 'nginx': 'nginx', 'nsis': 'NSIS',
'objectivec': 'Objective-C', 'ocaml': 'OCaml', 'php': 'PHP', 'php-extras': 'PHPExtras',
'powershell': 'PowerShell', 'jsx': 'React JSX', 'rest': 'reST (reStructuredText)',
'sas': 'SAS', 'sass': 'Sass (Sass)', 'scss': 'Sass (Scss)', 'sql': 'SQL', 'typescript': 'TypeScript', 'vhdl': 'VHDL',
'vim': 'vim', 'wiki': 'Wikimarkup', 'yaml': 'YAML'};
    // 高亮前处理
    Prism.hooks.add('before-highlight', function (env) {
        var pre = env.element.parentNode;
        if (!pre || !/pre/i.test(pre.nodeName)) {
            return;
        }
        var language = Languages[env.language] || (env.language.substring(0, 1).toUpperCase() +
         env.language.substring(1));
        pre.setAttribute('data-language', language);
    });
})(window, window.Prism);



